@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
*{box-sizing:border-box}ul{padding:0;list-style:none}button{border:none;background-color:transparent}.App{margin:0 auto;max-width:1200px;padding:20px;font-family:Roboto, sans-serif;font-size:28px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.tabs-list{display:-webkit-flex;display:flex;border-bottom:1px solid gray;min-width:700px;padding:0 40px}.tabs-list__item{font-size:18px;padding:10px 20px;position:relative;top:1px;cursor:pointer}.tabs-list__item:hover{background-color:#e0dede}.tabs-list__item:focus{outline:none}.tabs-list__item--active{border-left:1px solid;border-right:1px solid;border-top:1px solid;border-color:gray;border-bottom:1px solid white}

